.notFound {
  min-height: 70vh;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 40px 20px;
  background: #f8f9fa;
}

.notFound__card {
  background: white;
  padding: 40px;
  border-radius: 14px;
  max-width: 520px;
  width: 100%;
  text-align: center;
  box-shadow: 0 8px 30px rgba(0,0,0,0.08);
}

.notFound__card h1 {
  margin: 0;
  font-size: 72px;
  color: #3498db;
}

.notFound__card h2 {
  margin: 10px 0 10px 0;
  color: #2c3e50;
}

.notFound__card p {
  margin: 0;
  color: #666;
}

.notFound__actions {
  display: flex;
  gap: 12px;
  justify-content: center;
  margin-top: 22px;
  flex-wrap: wrap;
}
