.dashboard {
  max-width: 1200px;
  margin: 0 auto;
  padding: 40px 20px;
  min-height: 80vh;
}

.dashboard__header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 40px;
  padding-bottom: 20px;
  border-bottom: 2px solid #f0f0f0;
}

.dashboard__header h1 {
  margin: 0;
  color: #333;
}

.dashboard__userType {
  color: #666;
  margin-top: 5px;
  font-size: 16px;
}

.dashboard__section {
  margin-bottom: 50px;
}

.dashboard__section__header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.dashboard__section h2 {
  color: #333;
  margin-bottom: 20px;
}

.dashboard__empty {
  text-align: center;
  padding: 60px 20px;
  color: #999;
  font-size: 16px;
  background: #f9f9f9;
  border-radius: 10px;
}

.dashboard__listings {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
  gap: 20px;
}

.dashboard__card {
  cursor: pointer;
  transition: transform 0.2s;
}

.dashboard__card:hover {
  transform: translateY(-5px);
  box-shadow: 0 5px 20px rgba(0, 0, 0, 0.1);
}

.dashboard__card img {
  width: 100%;
  height: 200px;
  object-fit: cover;
}

.dashboard__card h3 {
  margin: 10px 0;
  font-size: 18px;
  color: #333;
}

.dashboard__card p {
  color: #666;
  font-size: 14px;
  margin: 5px 0;
}

.dashboard__price {
  font-weight: bold;
  color: #FF5B60 !important;
  font-size: 16px !important;
  margin-top: 10px !important;
}

.dashboard__card__actions {
  display: flex;
  gap: 10px;
  margin-top: 15px;
  padding-top: 15px;
  border-top: 1px solid #f0f0f0;
}

.dashboard__bookings {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.dashboard__booking {
  border-left: 4px solid #FF5B60;
}

.dashboard__booking__header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}

.dashboard__booking h3 {
  margin: 0 0 10px 0;
  color: #333;
  font-size: 18px;
}

.dashboard__booking__info {
  color: #666;
  margin: 5px 0;
  font-size: 14px;
}

.dashboard__booking__date {
  color: #FF5B60;
  font-weight: 600;
  margin: 5px 0;
}

.dashboard__booking__notes {
  color: #666;
  font-style: italic;
  margin-top: 10px;
  padding: 10px;
  background: #f9f9f9;
  border-radius: 5px;
}

.dashboard__booking__status {
  text-align: right;
}

.dashboard__booking__price {
  font-weight: bold;
  color: #333;
  margin-top: 10px;
  font-size: 18px;
}

.dashboard__booking__actions {
  margin-top: 20px;
  padding-top: 15px;
  border-top: 1px solid #f0f0f0;
}

@media screen and (max-width: 768px) {
  .dashboard__header {
    flex-direction: column;
    align-items: flex-start;
    gap: 15px;
  }

  .dashboard__section__header {
    flex-direction: column;
    align-items: flex-start;
    gap: 15px;
  }

  .dashboard__booking__header {
    flex-direction: column;
    gap: 15px;
  }

  .dashboard__booking__status {
    text-align: left;
  }
}
