.chatPage {
  display: flex;
  flex-direction: column;
  height: calc(100vh - 64px); /* adjust if header/footer differ */
  background: #f3f4f6;
}

/* Centered state (loading / not found) */
.chatPage--center {
  display: flex;
  align-items: center;
  justify-content: center;
}

/* Header */
.chatPage__header {
  padding: 8px 16px;
  border-bottom: 1px solid #e5e7eb;
  background: #ffffff;
}

.chatPage__headerLeft {
  display: flex;
  align-items: center;
  gap: 8px;
}

.chatPage__headerLeft h2 {
  margin: 0;
  font-size: 1rem;
  color: #111827;
}

.chatPage__subtitle {
  margin: 0;
  font-size: 0.8rem;
  color: #6b7280;
}

/* âš  Warning banner */
.chatPage__warning {
  background: #fef3c7;
  border-bottom: 1px solid #fbbf24;
  padding: 8px 16px;
}

.chatPage__warning p {
  margin: 0;
  font-size: 0.8rem;
  color: #92400e;
}

/* Body (messages area) */
.chatPage__body {
  flex: 1;
  overflow-y: auto;
  padding: 12px 16px;
  display: flex;
  flex-direction: column;
}

.chatPage__loading,
.chatPage__empty {
  margin-top: 16px;
  text-align: center;
  color: #6b7280;
  font-size: 0.9rem;
}

.chatPage__messages {
  display: flex;
  flex-direction: column;
  gap: 6px;
}

/* Message rows */
.chatPage__messageRow {
  display: flex;
}

.chatPage__messageRow--mine {
  justify-content: flex-end;
}

.chatPage__messageRow--theirs {
  justify-content: flex-start;
}

/* Message bubbles */
.chatPage__messageBubble {
  max-width: 70%;
  padding: 8px 10px;
  border-radius: 12px;
  font-size: 0.9rem;
  box-shadow: 0 1px 4px rgba(15, 23, 42, 0.16);
}

.chatPage__messageBubble--mine {
  background: #2563eb;
  color: #ffffff;
  border-bottom-right-radius: 4px;
}

.chatPage__messageBubble--theirs {
  background: #ffffff;
  color: #111827;
  border-bottom-left-radius: 4px;
}

.chatPage__messageText {
  margin: 0 0 2px;
  white-space: pre-wrap;
  word-wrap: break-word;
}

.chatPage__messageMeta {
  font-size: 0.7rem;
  opacity: 0.85;
}

/* Input bar */
.chatPage__inputBar {
  padding: 8px 12px;
  border-top: 1px solid #e5e7eb;
  background: #ffffff;
  display: flex;
  gap: 8px;
  align-items: flex-end;
}

.chatPage__sendButton {
  margin-bottom: 2px;
}

/* Responsive */
@media (max-width: 600px) {
  .chatPage {
    height: calc(100vh - 56px);
  }

  .chatPage__messageBubble {
    max-width: 80%;
  }
}
