.home {
  min-height: calc(100vh - 120px);
  background: #f3f4f6;
}

/* Hero */
.home__hero {
  padding: 60px 16px 40px;
  background: radial-gradient(circle at top left, #60a5fa, #1d4ed8);
  color: #ffffff;
  display: flex;
  justify-content: center;
}

.home__heroInner {
  width: 100%;
  max-width: 1100px;
  display: flex;
  flex-direction: column;
  gap: 24px;
}

.home__heroTextBlock h1 {
  font-size: 2.1rem;
  margin: 0 0 8px;
}

.home__heroTextBlock p {
  margin: 0;
  max-width: 620px;
  opacity: 0.96;
}

/* Search form */
.home__searchForm {
  display: flex;
  gap: 12px;
  padding: 12px;
  background: #ffffff;
  border-radius: 14px;
  box-shadow: 0 16px 45px rgba(15, 23, 42, 0.35);
  align-items: center;
}

.home__searchButton {
  white-space: nowrap;
  height: 100%;
}

/* Cards section */
.home__section {
  max-width: 1100px;
  margin: 32px auto 40px;
  padding: 0 16px 16px;
}

.home__sectionHeader h2 {
  margin: 0 0 4px;
  font-size: 1.45rem;
  color: #111827;
}

.home__sectionHeader p {
  margin: 0;
  color: #6b7280;
  font-size: 0.95rem;
}

.home__empty {
  margin-top: 24px;
  color: #6b7280;
}

.home__cards {
  display: grid;
  grid-template-columns: repeat(3, minmax(0, 1fr));
  gap: 16px;
  margin-top: 20px;
}

.home__card {
  background: #ffffff;
  border-radius: 14px;
  overflow: hidden;
  cursor: pointer;
  display: flex;
  flex-direction: column;
  box-shadow: 0 12px 32px rgba(15, 23, 42, 0.16);
  transition: transform 0.12s ease, box-shadow 0.12s ease;
}

.home__card:hover {
  transform: translateY(-2px);
  box-shadow: 0 20px 45px rgba(15, 23, 42, 0.22);
}

.home__cardImageWrapper {
  width: 100%;
  padding-top: 60%;
  position: relative;
  overflow: hidden;
}

.home__cardImage {
  position: absolute;
  inset: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.home__cardBody {
  padding: 12px 14px 14px;
}

.home__cardBody h3 {
  margin: 0 0 4px;
  font-size: 1rem;
  color: #111827;
}

.home__cardLocation {
  margin: 0;
  font-size: 0.85rem;
  color: #6b7280;
}

.home__cardPrice {
  margin: 6px 0 0;
  font-weight: 600;
  color: #2563eb;
}

.home__cardMeta {
  margin: 2px 0 0;
  font-size: 0.8rem;
  color: #6b7280;
}

.home__sectionFooter {
  margin-top: 20px;
  display: flex;
  justify-content: flex-end;
}

/* Map section */
.home__mapSection {
  max-width: 1100px;
  margin: 0 auto 40px;
  padding: 0 16px 16px;
}

.home__mapHeader h2 {
  margin: 0 0 4px;
  font-size: 1.3rem;
  color: #111827;
}

.home__mapHeader p {
  margin: 0 0 12px;
  color: #6b7280;
  font-size: 0.9rem;
}

.home__mapWrapper {
  background: #ffffff;
  border-radius: 14px;
  box-shadow: 0 12px 32px rgba(15, 23, 42, 0.16);
  margin-top: 8px;
  overflow: visible;
}

/* Responsive tweaks */
@media (max-width: 900px) {
  .home__heroTextBlock h1 {
    font-size: 1.8rem;
  }

  .home__cards {
    grid-template-columns: repeat(2, minmax(0, 1fr));
  }
}

@media (max-width: 600px) {
  .home__hero {
    padding: 40px 12px 24px;
  }

  .home__heroInner {
    gap: 16px;
  }

  .home__searchForm {
    flex-direction: column;
    align-items: stretch;
  }

  .home__searchButton {
    width: 100%;
  }

  .home__cards {
    grid-template-columns: 1fr;
  }
}
