.auth {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 90vh;
  padding: 40px 20px;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
}

.auth__container {
  max-width: 480px;
  width: 100%;
  background: white;
  padding: 50px 40px;
  border-radius: 16px;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.3);
}

.auth__container h2 {
  margin-bottom: 10px;
  color: #2c3e50;
  font-size: 32px;
  font-weight: 700;
  text-align: center;
}

.auth__subtitle {
  text-align: center;
  color: #7f8c8d;
  margin-bottom: 30px;
  font-size: 14px;
}

.auth__error {
  background-color: #ffebee;
  color: #c62828;
  padding: 14px;
  border-radius: 8px;
  margin-bottom: 20px;
  text-align: center;
  border-left: 4px solid #c62828;
  font-size: 14px;
}

.auth__success {
  background-color: #e8f5e9;
  color: #2e7d32;
  padding: 14px;
  border-radius: 8px;
  margin-bottom: 20px;
  text-align: center;
  border-left: 4px solid #2e7d32;
  font-size: 14px;
}

.auth form {
  display: flex;
  flex-direction: column;
}

.auth__usertype {
  margin: 20px 0 !important;
}

.auth__usertype legend {
  color: #2c3e50;
  font-weight: 600;
}

.auth__submit {
  background-color: #3498db !important;
  color: white !important;
  margin-top: 25px !important;
  padding: 14px !important;
  font-weight: 600 !important;
  font-size: 16px !important;
  text-transform: none !important;
  border-radius: 8px !important;
  transition: all 0.3s ease !important;
}

.auth__submit:hover:not(:disabled) {
  background-color: #2980b9 !important;
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(52, 152, 219, 0.4) !important;
}

.auth__submit:disabled {
  background-color: #bdc3c7 !important;
}

.auth__toggle {
  text-align: center;
  margin-top: 25px;
}

.auth__toggle p {
  color: #7f8c8d;
  font-size: 14px;
}

.auth__toggle span {
  color: #3498db;
  font-weight: 600;
  cursor: pointer;
  transition: color 0.2s ease;
}

.auth__toggle span:hover {
  color: #2980b9;
  text-decoration: underline;
}

.auth__guest {
  margin-top: 20px;
  text-align: center;
}

.auth__guest p {
  color: #7f8c8d;
  font-size: 14px;
  margin-bottom: 10px;
}

/* Material-UI TextField customization */
.auth .MuiOutlinedInput-root.Mui-focused .MuiOutlinedInput-notchedOutline {
  border-color: #3498db;
}

.auth .MuiFormLabel-root.Mui-focused {
  color: #3498db;
}

.auth .MuiRadio-colorSecondary.Mui-checked {
  color: #3498db;
}

@media screen and (max-width: 768px) {
  .auth__container {
    padding: 40px 30px;
  }

  .auth__container h2 {
    font-size: 26px;
  }
}

@media screen and (max-width: 480px) {
  .auth {
    padding: 20px 15px;
  }

  .auth__container {
    padding: 30px 25px;
  }

  .auth__container h2 {
    font-size: 24px;
  }
}
