.favorites {
  display: flex;
  justify-content: center;
  padding: 40px 20px;
  min-height: 70vh;
  background: #f8f9fa;
}

.favorites__container {
  width: 100%;
  max-width: 1000px;
  background: white;
  border-radius: 12px;
  padding: 30px;
  box-shadow: 0 4px 20px rgba(0,0,0,0.08);
}

.favorites__header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 16px;
  margin-bottom: 20px;
}

.favorites__header h2 {
  margin: 0;
  display: flex;
  align-items: center;
  color: #2c3e50;
}

.favorites__note {
  color: #666;
}

.favorites__empty {
  padding: 30px;
  border: 2px dashed #e0e0e0;
  border-radius: 10px;
  text-align: center;
  color: #555;
}

.favorites__list {
  display: flex;
  flex-direction: column;
  gap: 14px;
}

.favorites__item {
  display: grid;
  grid-template-columns: 180px 1fr auto;
  gap: 16px;
  padding: 14px;
  border: 1px solid #eee;
  border-radius: 12px;
  cursor: pointer;
  transition: transform 120ms ease, box-shadow 120ms ease;
}

.favorites__item:hover {
  transform: translateY(-2px);
  box-shadow: 0 10px 30px rgba(0,0,0,0.08);
}

.favorites__image {
  width: 180px;
  height: 120px;
  object-fit: cover;
  border-radius: 10px;
}

.favorites__info h3 {
  margin: 0 0 6px 0;
  color: #2c3e50;
}

.favorites__location {
  margin: 0 0 6px 0;
  color: #666;
}

.favorites__price {
  margin: 0;
  font-weight: 700;
  color: #3498db;
}

.favorites__actions {
  display: flex;
  align-items: center;
}

@media (max-width: 768px) {
  .favorites__item {
    grid-template-columns: 1fr;
  }

  .favorites__image {
    width: 100%;
    height: 200px;
  }

  .favorites__actions {
    justify-content: flex-end;
  }
}
