.listingDetail {
  padding: 16px;
  background: #f3f4f6;
  min-height: calc(100vh - 120px); /* header + footer space */
}

/* Centered states */
.listingDetail--center {
  display: flex;
  align-items: center;
  justify-content: center;
}

/* Top back button */
.listingDetail__topBar {
  max-width: 1100px;
  margin: 0 auto 8px;
}

/* Main container */
.listingDetail__container {
  max-width: 1100px;
  margin: 0 auto 24px;
  display: grid;
  grid-template-columns: minmax(0, 2fr) minmax(0, 1fr);
  gap: 24px;
}

/* Left column */
.listingDetail__left {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.listingDetail__titleBlock {
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.listingDetail__title {
  margin: 0;
  font-size: 1.7rem;
  color: #111827;
}

.listingDetail__location {
  margin: 0;
  font-size: 0.9rem;
  color: #6b7280;
}

/* Meta pills */
.listingDetail__metaRow {
  margin-top: 8px;
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
}

.listingDetail__metaPill {
  font-size: 0.8rem;
  padding: 4px 8px;
  border-radius: 999px;
  background: #e5e7eb;
  color: #374151;
}

/* Image section */
.listingDetail__imageGrid {
  border-radius: 16px;
  overflow: hidden;
  background: #000;
}

.listingDetail__imageMainWrapper {
  position: relative;
  width: 100%;
  padding-top: 60%; /* 16:9ish */
}

.listingDetail__imageMain {
  position: absolute;
  inset: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

/* Sections */
.listingDetail__section {
  background: #ffffff;
  border-radius: 16px;
  padding: 16px 18px 18px;
  box-shadow: 0 8px 24px rgba(15, 23, 42, 0.12);
}

.listingDetail__sectionTitle {
  margin: 0 0 8px;
  font-size: 1.1rem;
  color: #111827;
}

.listingDetail__description {
  margin: 0;
  font-size: 0.93rem;
  color: #4b5563;
}

/* Amenities */
.listingDetail__amenities {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
  margin-top: 6px;
}

.listingDetail__amenityChip {
  font-size: 0.8rem !important;
}

/* Right column: booking card */
.listingDetail__right {
  display: flex;
  flex-direction: column;
}

.listingDetail__bookingCard {
  background: #ffffff;
  border-radius: 16px;
  padding: 16px 18px 18px;
  box-shadow: 0 10px 30px rgba(15, 23, 42, 0.16);
  display: flex;
  flex-direction: column;
  gap: 12px;
  position: sticky;
  top: 80px; /* sticks under header on tall screens */
}

/* Price row */
.listingDetail__priceRow {
  display: flex;
  justify-content: space-between;
  align-items: baseline;
}

.listingDetail__price {
  font-size: 1.4rem;
  font-weight: 600;
  color: #111827;
}

.listingDetail__priceUnit {
  font-size: 0.9rem;
  color: #6b7280;
}

/* Host block */
.listingDetail__hostBlock {
  margin-top: 4px;
}

.listingDetail__hostLabel {
  margin: 0;
  font-size: 0.78rem;
  text-transform: uppercase;
  letter-spacing: 0.06em;
  color: #9ca3af;
}

.listingDetail__hostName {
  margin: 0;
  font-size: 0.96rem;
  color: #111827;
  font-weight: 500;
}

.listingDetail__hostContact {
  margin: 2px 0 0;
  font-size: 0.82rem;
  color: #4b5563;
}

/* Divider */
.listingDetail__divider {
  border-top: 1px solid #e5e7eb;
  margin: 4px 0;
}

/* Actions */
.listingDetail__actions {
  margin-top: 4px;
}

/* Info note */
.listingDetail__infoNote {
  margin-top: 4px;
}

.listingDetail__infoNote p {
  margin: 0;
  font-size: 0.82rem;
  color: #6b7280;
}

/* Dialog spacing overrides (optional) */
.MuiDialogContent-root p:first-of-type {
  margin-top: 0;
}

/* Responsive */
@media (max-width: 900px) {
  .listingDetail__container {
    grid-template-columns: minmax(0, 1fr);
  }

  .listingDetail__bookingCard {
    position: static;
  }
}

@media (max-width: 600px) {
  .listingDetail {
    padding: 12px;
  }

  .listingDetail__title {
    font-size: 1.4rem;
  }

  .listingDetail__section {
    border-radius: 12px;
  }

  .listingDetail__bookingCard {
    border-radius: 12px;
  }
}
