/* ApplyLandlord.css */
.page {
  max-width: 520px;
  margin: 40px auto;
  padding: 16px;
}

.title {
  margin: 0 0 12px 0;
}

.card {
  margin-bottom: 12px;
  padding: 12px;
  border: 1px solid #ddd;
  border-radius: 10px;
}

.row {
  margin-bottom: 10px;
}

.label {
  display: block;
  margin-bottom: 6px;
  font-weight: 600;
}

.input {
  width: 100%;
  padding: 10px;
  border-radius: 10px;
  border: 1px solid #ccc;
  outline: none;
}

.file {
  width: 100%;
}

.button {
  width: 100%;
  padding: 12px;
  border-radius: 10px;
  border: none;
  cursor: pointer;
}

.button:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

.error {
  color: crimson;
  margin-top: 12px;
}

.success {
  color: green;
  margin-top: 12px;
}

.muted {
  opacity: 0.75;
  font-size: 13px;
}
