.header {
  box-shadow: 0 2px 8px rgba(15, 23, 42, 0.08);
  background-color: #ffffff;
}

/* ðŸ”¹ Make header taller / roomier */
.header__toolbar {
  max-width: 1100px;
  margin: 0 auto;
  width: 100%;
  display: flex;
  align-items: center;
  padding: 10px 12px; /* was none â€“ adds vertical & slight horizontal padding */
}

/* Logo */
.header__logo {
  display: flex;
  align-items: center;
  cursor: pointer;
}

/* ðŸ”¹ Slightly larger logo bubble */
.header__logoMark {
  width: 34px;  /* was 30px */
  height: 34px; /* was 30px */
  border-radius: 999px;
  background: linear-gradient(135deg, #2563eb, #1d4ed8);
  display: inline-flex;
  align-items: center;
  justify-content: center;
  color: #ffffff;
  font-weight: 700;
  font-size: 0.95rem;
  margin-right: 10px;
}

.header__logoTextBlock {
  display: flex;
  flex-direction: column;
}

/* ðŸ”¹ Slightly larger title */
.header__logoTitle {
  font-weight: 600;
  color: #111827;
  font-size: 1.1rem; /* was 1rem */
}

.header__logoSubtitle {
  font-size: 0.75rem; /* was 0.7rem */
  color: #6b7280;
}

/* Nav */
.header__nav {
  display: flex;
  gap: 6px;
  margin-left: 40px;
  flex: 1;
}

/* Right side */
.header__right {
  display: flex;
  align-items: center;
  gap: 10px; /* was 8px */
}

.header__iconButton {
  margin-right: 4px;
}

/* Notification menu (if ever used again) */
.header__notifTitle {
  padding: 8px 16px 4px;
}

/* User button */
.header__userButton {
  text-transform: none !important;
  display: inline-flex;
  align-items: center;
  gap: 6px;
  padding: 5px 12px; /* slightly larger */
  border-radius: 999px;
  border: 1px solid #e5e7eb;
  background: #ffffff;
  cursor: pointer;
}

.header__userInitial {
  width: 26px;  /* was 24 */
  height: 26px; /* was 24 */
  border-radius: 999px;
  background-color: #2563eb;
  color: #ffffff;
  font-size: 0.85rem; /* was 0.8rem */
  display: inline-flex;
  align-items: center;
  justify-content: center;
}

.header__userName {
  max-width: 150px; /* a bit more room */
  font-size: 0.9rem; /* was 0.85rem */
  color: #111827;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

/* Simple text buttons on nav/right */
.header__buttonText {
  background: none;
  border: none;
  padding: 6px 10px;
  margin: 0;
  font-size: 0.9rem;
  color: #4b5563;
  cursor: pointer;
}

.header__buttonText:hover {
  color: #111827;
}

/* Login pill */
.header__loginButton {
  background-color: #2563eb;
  border: none;
  color: #ffffff;
  padding: 7px 16px;
  border-radius: 999px;
  font-size: 0.9rem;
  cursor: pointer;
}

.header__loginButton:hover {
  background-color: #1d4ed8;
}

/* Responsive */
@media (max-width: 900px) {
  .header__toolbar {
    padding-left: 8px;
    padding-right: 8px;
  }

  .header__nav {
    margin-left: 16px;
  }
}

@media (max-width: 700px) {
  .header__nav {
    display: none;
  }
}
