.supportDev {
  min-height: calc(100vh - 120px); /* header + footer area */
  background: #f3f4f6;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 16px;
}

.supportDev__card {
  max-width: 640px;
  width: 100%;
  background: #ffffff;
  border-radius: 16px;
  padding: 22px 24px 24px;
  box-shadow: 0 12px 32px rgba(15, 23, 42, 0.14);
}

.supportDev__card h1 {
  margin: 0 0 6px;
  font-size: 1.6rem;
  color: #111827;
}

.supportDev__subtitle {
  margin: 0 0 16px;
  font-size: 0.95rem;
  color: #4b5563;
}

.supportDev__section {
  margin-top: 18px;
}

.supportDev__section h2 {
  margin: 0 0 4px;
  font-size: 1rem;
  color: #111827;
}

.supportDev__hint {
  margin: 0 0 10px;
  font-size: 0.8rem;
  color: #6b7280;
}

/* Image box */
.supportDev__imageWrapper {
  margin-top: 4px;
  padding: 10px 12px;
  border-radius: 12px;
  background: #f9fafb;
  border: 1px solid #e5e7eb;
  display: flex;
  align-items: center;
  justify-content: center;
}

.supportDev__imageWrapper--gcash {
  background: #eff6ff;
  border-color: #bfdbfe;
}

/* Actual image */
.supportDev__image {
  max-width: 100%;
  height: auto;
  border-radius: 10px;
  display: block;
  box-shadow: 0 6px 18px rgba(15, 23, 42, 0.18);
}

/* Small text under images */
.supportDev__imageNote {
  margin-top: 6px;
  font-size: 0.78rem;
  color: #6b7280;
}

.supportDev__thanks {
  margin-top: 18px;
  font-size: 0.9rem;
  color: #374151;
}

.supportDev__actions {
  margin-top: 16px;
  display: flex;
  justify-content: flex-end;
}

@media (max-width: 640px) {
  .supportDev__card {
    padding: 18px 16px 20px;
  }
}
