.searchPage {
    max-width: 1400px;
    margin: 0 auto;
    padding: 20px;
}

.searchPage__info {
    padding: 30px;
    background: white;
    border-radius: 12px;
    box-shadow: 0 2px 12px rgba(0, 0, 0, 0.08);
    margin-bottom: 30px;
}

.searchPage__count {
    margin-bottom: 10px;
    font-size: 14px;
    color: #7f8c8d;
    font-weight: 500;
}

.searchPage__info > h1 {
    margin-bottom: 25px;
    color: #2c3e50;
    font-size: 32px;
    font-weight: 700;
}

.searchPage__filters {
    display: flex;
    gap: 15px;
    flex-wrap: wrap;
    margin-top: 20px;
}

.filter-control {
    min-width: 180px;
}

.price-filter-btn {
    background-color: #3498db !important;
    color: white !important;
    text-transform: none !important;
    border-radius: 8px !important;
    padding: 8px 20px !important;
    font-weight: 600 !important;
    transition: all 0.3s ease !important;
}

.price-filter-btn:hover {
    background-color: #2980b9 !important;
    transform: translateY(-2px);
    box-shadow: 0 4px 12px rgba(52, 152, 219, 0.3) !important;
}

.searchPage__priceFilter {
    margin-top: 25px;
    padding: 25px;
    background: #f8f9fa;
    border-radius: 12px;
    max-width: 500px;
    border: 2px solid #e0e0e0;
}

.price-label {
    color: #2c3e50;
    font-weight: 600;
    font-size: 16px;
}

.price-slider .MuiSlider-thumb {
    color: #3498db;
}

.price-slider .MuiSlider-track {
    color: #3498db;
}

.price-slider .MuiSlider-rail {
    color: #bdc3c7;
}

.searchPage__results {
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.searchPage__noResults {
    text-align: center;
    padding: 80px 20px;
    background: white;
    border-radius: 12px;
    box-shadow: 0 2px 12px rgba(0, 0, 0, 0.08);
}

.noResults__icon {
    font-size: 64px;
    margin-bottom: 20px;
    opacity: 0.5;
}

.searchPage__noResults h3 {
    margin-bottom: 12px;
    color: #2c3e50;
    font-size: 24px;
    font-weight: 700;
}

.searchPage__noResults p {
    color: #7f8c8d;
    font-size: 16px;
}

/* Material-UI Select customization */
.searchPage .MuiOutlinedInput-root.Mui-focused .MuiOutlinedInput-notchedOutline {
    border-color: #3498db;
}

@media screen and (max-width: 768px) {
    .searchPage {
        padding: 15px;
    }

    .searchPage__info {
        padding: 20px;
    }

    .searchPage__info > h1 {
        font-size: 24px;
    }

    .searchPage__filters {
        flex-direction: column;
    }

    .filter-control {
        width: 100%;
        min-width: auto;
    }

    .price-filter-btn {
        width: 100%;
    }

    .searchPage__priceFilter {
        max-width: 100%;
    }
}
